/** @jsx dom */var react = require('react')
  , reactIntlMixin = require('react-intl')
  , internalMixin = require('../mixin/internalClick.js')
  , app = require('../app.js')
  , dom = react.DOM;

var playlistItem = react.createClass({
  displayName: 'Playlist Item',
  mixins: [internalMixin, reactIntlMixin],

  render: function() {
    return (
      dom.li(null, 
        dom.a({href: '/watch/'+this.props.video.isrc, onClick: this.internalRouteOnClick('/watch/'+this.props.video.isrc), className: "videoLink"}, 
          dom.span({className: "image"}, 
            dom.img({src: this.props.video.artistImg}), 
            dom.span({className: "video-time"}, this.props.video.duration, "s", dom.br(null)), 
            dom.span({className: "video-count"}, this.formatNumber((this.props.video.views && this.props.video.views.total) || 0), " views", dom.br(null))
          ), 
          dom.span({className: "title"}, this.props.video.title), 
          dom.span({className: "by"}, 
            "by ", dom.b(null, dom.span({className: ""}, this.props.video.artist))
          )
        )
      )
      );
  }
})

var playlist = react.createClass({
  displayName: 'Playlist',

  getInitialState: function() {
    return {
      videos: this.props.playlist.videos
    }
  },

  componentDidMount: function() {
    console.log('@@componentDidMount', this.props);
    var id = this.props.playlist.id;
    this.listenTo = app.eventChannel.filter(function(val) {return val.videos && val.playlistId == id;}).onValue(this.onPlaylistUpdate.bind(this));
  },

  componentWillUnmount: function() {
    console.log('@@componentWillUnmount', this.stream);
    this.listenTo(); // remove the listener
  },

  onPlaylistUpdate: function(value) {
    console.log('onPlaylistUpdate', value);
    this.setState({videos: value.videos});
  },

  render: function () {
    return (
      dom.div({className: "playlist-component"}, 
        dom.ul(null, 
          this.state.videos.map(function(video,ix) {return (playlistItem({key: ix, video: video}));})
        )
      )
      );
  }
});

module.exports = playlist;




