/** @jsx dom */var react = require('react')
  , internalMixin = require('../mixin/internalClick.js')
  , dom = react.DOM;

var header = react.createClass({
  displayName: 'App Header',
  mixins: [internalMixin],

  render: function () {
    return (
      dom.header({id: "nav"}, 
        dom.div({className: "container"}, 
          dom.div({className: "logo", href: "/", onClick: this.internalClick}), 
          dom.ul(null, 
            dom.li(null, dom.a({href: "/tv", onClick: this.internalClick}, "vevo tv")), 
            dom.li(null, dom.a({href: "/search", onClick: this.internalClick}, "search")), 
            dom.li(null, dom.a({href: "/browse", onClick: this.internalClick}, "browse")), 
            dom.li(null, dom.a({href: "/browse/shows", onClick: this.internalClick}, "shows"))
          )
        )
      )
      );
  }
});

module.exports = header;


