/** @jsx dom */var react = require('react')
  , reactIntlMixin = require('react-intl')
  , dom = react.DOM;

var videoDetails = react.createClass({

  displayName: 'videoDetails',
  mixins: [reactIntlMixin],

  getInitialState: function() {
    return {
      showCredits: false
    }
  },

  // http://img.cache.vevo.com/Content/VevoImages/artist/3231858819325FD8D84FEE279C8DA83020144818251216.jpg?width=-2&height=700&crop=auto

  toggleCredits: function() {
    console.log('toggle', this.state.showCredits);
    this.setState({
      showCredits: !this.state.showCredits
    });
  },

  render: function () {
    return (
      dom.section({className: "video-detail-component"}, 
        dom.div({className: "container"}, 
          dom.div({className: "details"}, 
            dom.div({className: "artist"}, 
              dom.div({className: "view-count"}, 
                this.formatNumber(this.props.video.views.total), " ", dom.span(null, "views")
              ), 
              dom.img({src: this.props.video.artistImg}), 
              dom.h1(null, this.props.video.title), 
              dom.h2(null, dom.a({href: this.props.video.artistUrl}, this.props.video.artist)), 
              dom.p(null, "Directed by Francesco Carrozzini", 
                dom.a({style: {display:(this.state.showCredits?'none':'display')}, onClick: this.toggleCredits}, "More info..."), 
                dom.a({style: {display:(this.state.showCredits?'display':'none')}, onClick: this.toggleCredits}, "Hide info")
              )
            ), 
            dom.div(null, 
              dom.ul(null, 
                dom.li(null, dom.a({className: "action share share-icon-vevo"})), 
                dom.li(null, dom.a({className: "action add add-icon-vevo"})), 
                dom.li(null, dom.a({className: "info-icon-vevo action info"}))
              ), 
              dom.div({className: "buy"}, 
                dom.p(null, "Buy on", dom.br(null), dom.a(null, "Amazon"), "|", dom.a(null, "iTunes"))
              )
            )
          ), 
          dom.div({className: "ad"}, 
            dom.img({src: "https://sassets.vevo.com/adsales/v3/McDonalds/2014Q3_LIFT_McCafeMcMuffin/2014.0805_MCD_LIFT_CONT1_CONTENT_RB_300x60.jpg"})
          )
        )
      )
      );
  }

});

module.exports = videoDetails;


