/** @jsx dom */var app = require('../app.js')
  , dom = app.dom
  , vevoAPI = require('../../data-access-layer');

var browsePage = app.react.createClass({
  displayName: 'Browse Page',

  render: function () {
    return (
      dom.div({className: "page broswe-page container"}, 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/premieres"}, "Premieres"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Top Videos This Week"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Staff Picks"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Live Performances"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Popular Artists"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Emerging Artists, presented by McDonalds"))
        ), 
        dom.section(null, 
          dom.h2(null, dom.a({href: "/browse/xx"}, "Certified Artists"))
        )
      )
    )
    ;
  }
});

/*
 * Register component and routes for the browse page.
 */
app.registerRootComponent(browsePage);
app.routes.addRoute('/browse', function(details, renderFn, isrc) {
  renderFn(browsePage(), {title:'Browse VEVO'});
});



